
.loading-container{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;height:100%;width:100%
}
.state-loading{height:150px;width:106px;text-align:center
}
.state-loading .cord{padding-top:100px;width:15px;transform:rotate(0deg);transform-origin:50% 50%;float:left
}
.state-loading .ball{background:#222;width:15px;height:15px;float:left;border-radius:50%
}
.state-loading .shadows{clear:left;padding-top:20px;margin-left:-2px
}
.state-loading .shadows div{float:left;margin-left:2px;width:13px;height:3px;border-radius:50%;box-shadow:0 0 3px rgba(204,204,204,0.3);background:rgba(204,204,204,0.3)
}
.state-loading .leftMove{animation:leftBall .5s ease-in-out 0s infinite alternate
}
.state-loading .rightMove{animation:rightBall .5s ease-in-out 0s infinite alternate
}
.state-loading .leftShadow{animation:leftShadowN .5s ease-in-out 0s infinite alternate
}
.state-loading .rightShadow{animation:rightShadowN .5s ease-in-out 0s infinite alternate
}
@keyframes leftBall{
0%{transform:rotate(0deg) translateY(0px)
}
50%{transform:rotate(0deg) translateY(0px)
}
100%{transform:rotate(50deg) translateY(-20px)
}
}
@keyframes rightBall{
0%{transform:rotate(-50deg) translateY(-20px)
}
50%{transform:rotate(0deg) translateY(0px)
}
100%{transform:rotate(0deg) translateY(0px) translateX(0px)
}
}
@keyframes leftShadowN{
0%{transform:translateX(0px)
}
50%{transform:translateX(0px)
}
100%{transform:translateX(-25px)
}
}
@keyframes rightShadowN{
0%{transform:translateX(25px)
}
50%{transform:translateY(0px)
}
100%{transform:translateY(0px)
}
}



