
.input-radio{margin-right:1rem;cursor:pointer
}
.input-radio>input[type=radio],.input-radio>input[type=checkbox]{position:absolute;clip:rect(0, 0, 0, 0);pointer-events:none
}
.input-radio .c-indicator{border:1px solid #e7e7e7;border-radius:1000px;padding:.5rem 1.75rem
}
.input-radio .c-indicator.checked{border-color:#20a0ff;color:#20a0ff
}
.radio-button>.btn>input[type=radio],.radio-button>.btn>input[type=checkbox]{position:absolute;clip:rect(0, 0, 0, 0);pointer-events:none
}



