
.pull-to-refresh-container{clear:both;text-align:center
}
.pull-to-refresh-container .loading-default{position:relative;border:1px solid #999;animation:ease loading-rotating 1.5s infinite
}
.pull-to-refresh-container .loading-default:before{content:'';position:absolute;display:block;top:0;left:50%;margin-top:-3px;margin-left:-3px;width:6px;height:6px;background-color:#999;border-radius:50%
}
.pull-to-refresh-container .loading-spiral{border:2px solid #777;border-right-color:transparent;animation:linear loading-rotating .85s infinite
}
.pull-to-refresh-container .loading-bubbles{position:relative
}
.pull-to-refresh-container .loading-bubbles:before{content:'';position:absolute;left:50%;top:50%;margin-top:-.5px;margin-left:-.5px;width:1px;height:1px;border-radius:50%;animation:linear loading-bubbles .85s infinite
}
@keyframes loading-bubbles{
0%{box-shadow:-12px 0 .4px #666,0px 0 .8px #666,12px 0 0 1.2px #666,8.52px 8.52px 0 1.6px #666,0 12px 0 2px #666,-8.52px 8.52px 0 2.4px #666,-12px 0 0 2.8px #666,-17.04px 0 3.2px #666
}
12.5%{box-shadow:-12px 0 3.2px #666,0px 0 .4px #666,12px 0 0 .8px #666,8.52px 8.52px 0 1.2px #666,0 12px 0 1.6px #666,-8.52px 8.52px 0 2px #666,-12px 0 0 2.4px #666,-17.04px 0 2.8px #666
}
25%{box-shadow:-12px 0 2.8px #666,0px 0 3.2px #666,12px 0 0 .4px #666,8.52px 8.52px 0 .8px #666,0 12px 0 1.2px #666,-8.52px 8.52px 0 1.6px #666,-12px 0 0 2px #666,-17.04px 0 2.4px #666
}
37.5%{box-shadow:-12px 0 2.4px #666,0px 0 2.8px #666,12px 0 0 3.2px #666,8.52px 8.52px 0 .4px #666,0 12px 0 .8px #666,-8.52px 8.52px 0 1.2px #666,-12px 0 0 1.6px #666,-17.04px 0 2px #666
}
50%{box-shadow:-12px 0 2px #666,0px 0 2.4px #666,12px 0 0 2.8px #666,8.52px 8.52px 0 3.2px #666,0 12px 0 .4px #666,-8.52px 8.52px 0 .8px #666,-12px 0 0 1.2px #666,-17.04px 0 1.6px #666
}
62.5%{box-shadow:-12px 0 1.6px #666,0px 0 2px #666,12px 0 0 2.4px #666,8.52px 8.52px 0 2.8px #666,0 12px 0 3.2px #666,-8.52px 8.52px 0 .4px #666,-12px 0 0 .8px #666,-17.04px 0 1.2px #666
}
75%{box-shadow:-12px 0 1.2px #666,0px 0 1.6px #666,12px 0 0 2px #666,8.52px 8.52px 0 2.4px #666,0 12px 0 2.8px #666,-8.52px 8.52px 0 3.2px #666,-12px 0 0 .4px #666,-17.04px 0 .8px #666
}
87.5%{box-shadow:-12px 0 .8px #666,0px 0 1.2px #666,12px 0 0 1.6px #666,8.52px 8.52px 0 2px #666,0 12px 0 2.4px #666,-8.52px 8.52px 0 2.8px #666,-12px 0 0 3.2px #666,-17.04px 0 .4px #666
}
100%{box-shadow:-12px 0 .4px #666,0px 0 .8px #666,12px 0 0 1.2px #666,8.52px 8.52px 0 1.6px #666,0 12px 0 2px #666,-8.52px 8.52px 0 2.4px #666,-12px 0 0 2.8px #666,-17.04px 0 3.2px #666
}
}
.pull-to-refresh-container .loading-circles{position:relative
}
.pull-to-refresh-container .loading-circles:before{content:'';position:absolute;left:50%;top:50%;margin-top:-2.5px;margin-left:-2.5px;width:5px;height:5px;border-radius:50%;animation:linear loading-circles .75s infinite
}
@keyframes loading-circles{
0%{box-shadow:-12px 0 #505050,0px 0 #646464,12px 0 0 #797979,8.52px 8.52px 0 #8d8d8d,0 12px 0 #a2a2a2,-8.52px 8.52px 0 #b6b6b6,-12px 0 0 #cacaca,-17.04px 0 #dfdfdf
}
12.5%{box-shadow:-12px 0 #dfdfdf,0px 0 #505050,12px 0 0 #646464,8.52px 8.52px 0 #797979,0 12px 0 #8d8d8d,-8.52px 8.52px 0 #a2a2a2,-12px 0 0 #b6b6b6,-17.04px 0 #cacaca
}
25%{box-shadow:-12px 0 #cacaca,0px 0 #dfdfdf,12px 0 0 #505050,8.52px 8.52px 0 #646464,0 12px 0 #797979,-8.52px 8.52px 0 #8d8d8d,-12px 0 0 #a2a2a2,-17.04px 0 #b6b6b6
}
37.5%{box-shadow:-12px 0 #b6b6b6,0px 0 #cacaca,12px 0 0 #dfdfdf,8.52px 8.52px 0 #505050,0 12px 0 #646464,-8.52px 8.52px 0 #797979,-12px 0 0 #8d8d8d,-17.04px 0 #a2a2a2
}
50%{box-shadow:-12px 0 #a2a2a2,0px 0 #b6b6b6,12px 0 0 #cacaca,8.52px 8.52px 0 #dfdfdf,0 12px 0 #505050,-8.52px 8.52px 0 #646464,-12px 0 0 #797979,-17.04px 0 #8d8d8d
}
62.5%{box-shadow:-12px 0 #8d8d8d,0px 0 #a2a2a2,12px 0 0 #b6b6b6,8.52px 8.52px 0 #cacaca,0 12px 0 #dfdfdf,-8.52px 8.52px 0 #505050,-12px 0 0 #646464,-17.04px 0 #797979
}
75%{box-shadow:-12px 0 #797979,0px 0 #8d8d8d,12px 0 0 #a2a2a2,8.52px 8.52px 0 #b6b6b6,0 12px 0 #cacaca,-8.52px 8.52px 0 #dfdfdf,-12px 0 0 #505050,-17.04px 0 #646464
}
87.5%{box-shadow:-12px 0 #646464,0px 0 #797979,12px 0 0 #8d8d8d,8.52px 8.52px 0 #a2a2a2,0 12px 0 #b6b6b6,-8.52px 8.52px 0 #cacaca,-12px 0 0 #dfdfdf,-17.04px 0 #505050
}
100%{box-shadow:-12px 0 #505050,0px 0 #646464,12px 0 0 #797979,8.52px 8.52px 0 #8d8d8d,0 12px 0 #a2a2a2,-8.52px 8.52px 0 #b6b6b6,-12px 0 0 #cacaca,-17.04px 0 #dfdfdf
}
}
.pull-to-refresh-container .loading-wave-dots{position:relative
}
.pull-to-refresh-container .loading-wave-dots:before{content:'';position:absolute;top:50%;left:50%;margin-left:-4px;margin-top:-4px;width:8px;height:8px;background-color:#bbb;border-radius:50%;animation:linear loading-wave-dots 2.8s infinite
}
@keyframes loading-wave-dots{
0%{box-shadow:-32px 0 0 #bbb,-16px 0 0 #bbb,16px 0 0 #bbb,32px 0 0 #bbb
}
5%{box-shadow:-32px -4px 0 #bbb,-16px 0 0 #bbb,16px 0 0 #bbb,32px 0 0 #bbb;transform:translateY(0)
}
10%{box-shadow:-32px -6px 0 #999,-16px -4px 0 #bbb,16px 0 0 #bbb,32px 0 0 #bbb;transform:translateY(0)
}
15%{box-shadow:-32px 2px 0 #bbb,-16px -2px 0 #999,20px 0 #bbb,36px 0 #bbb;transform:translateY(-4px);background-color:#bbb
}
20%{box-shadow:-26px 0 #bbb,-16px 4px 0 #bbb,16px 2px 0 #bbb,38px 0 #bbb;transform:translateY(-6px);background-color:#999
}
25%{box-shadow:-30px 0 #bbb,-14px 0 #bbb,16px -4px 0 #999,32px -2px 0 #bbb;transform:translateY(-2px);background-color:#bbb
}
30%{box-shadow:-32px 0 0 #bbb,-16px 0 0 #bbb,16px -2px 0 #bbb,32px -6px 0 #999;transform:translateY(0)
}
35%{box-shadow:-32px 0 0 #bbb,-16px 0 0 #bbb,16px 0 0 #bbb,32px -2px 0 #bbb
}
40%{box-shadow:-32px 0 0 #bbb,-16px 0 0 #bbb,16px 0 0 #bbb,32px 0 0 #bbb
}
100%{box-shadow:-32px 0 0 #bbb,-16px 0 0 #bbb,16px 0 0 #bbb,32px 0 0 #bbb
}
}
@keyframes loading-rotating{
0%{transform:rotate(0deg)
}
100%{transform:rotate(360deg)
}
}
.pull-to-refresh-status-prompt{color:#666;font-size:14px;text-align:center;padding:10px 0
}



